.report-expense {
    width: 50vw;
    height: 80vh;
    background: white;
    border-radius: 50px;
}

.report-header {
    color: black;
    font-weight: bold;
    font-size: 25px;
    /* background-color: red; */
    /* animation-name: example; */
    /* animation-duration: 3s; */
    border-radius: 25px;
    /* animation-iteration-count: infinite; */
    height: 30px;
    margin-bottom: 20px;
    display: inline-block;
    width: 50%;
    font-size: calc(5px + 2vmin);
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto auto auto;
    /* background-color: #2196F3; */
    padding: 10px;
}

.grid-item {
    background-color: whitesmoke;
    border: 1px solid rgba(0, 0, 0, 0.8);
    flex-direction: row;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid-headings {
    margin-bottom: 5px;
    font-weight: bold;
    padding:10px;
}

.editButton, .deleteButton {
    background-color: DodgerBlue;
    border: none;
    color: white;
    padding: 12px 16px;
    font-size: 16px;
    cursor: pointer;
    margin-right:5px;
    border-radius: 25px;
}